<template>
  <div>
    <v-container class="terapia-bilingue__container">
      <title-ribbon :title="this.$t('servicios.terapia-bilingue.titulo')" />
      <div class="terapia-bilingue_container__main">
        <v-row class="terapia-bilingue_row__img1">
          <v-col>
            <v-img
              src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto/v1639498861/terapiacl/servicios/terapia-bilingue/terapia-bilingue-1"
            />
          </v-col>
        </v-row>
        <v-row class="terapia-bilingue_row__langs">
          <v-col class="langs_col">
            <div class="langs-main">
              <div
                class="country"
                @click="changeLang('es')"
              >
                <span
                  :class="
                    localeClass.esSelected
                      ? 'langSpanSelected'
                      : 'langSpanNormal'
                  "
                  v-html="
                    $t('servicios.terapia-bilingue.texto-idiomas.espanol')
                  "
                />
                <v-img
                  class="imagen"
                  src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto/v1639498861/terapiacl/servicios/terapia-bilingue/chile"
                />
              </div>
              <div
                class="country"
                @click="changeLang('en')"
              >
                <span
                  :class="
                    localeClass.enSelected
                      ? 'langSpanSelected'
                      : 'langSpanNormal'
                  "
                  v-html="$t('servicios.terapia-bilingue.texto-idiomas.ingles')"
                />
                <v-img
                  class="imagen"
                  src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto/v1639498861/terapiacl/servicios/terapia-bilingue/england"
                />
              </div>
            </div>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="terapia-bilingue_row__wrap1">
          <v-col class="wrap1_col">
            <div class="wrap1_main">
              <p v-html="this.$t('servicios.terapia-bilingue.texto')" />
            </div>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="terapia-bilingue_row__boxes">
          <v-col class="col-12 col-md-6 box-formulario">
            <div class="main">
              <div class="up">
                <h4
                  v-html="
                    this.$t(
                      'servicios.terapia-bilingue.diptico.sec-formulario.titulo'
                    )
                  "
                />
                <span
                  v-html="
                    this.$t(
                      'servicios.terapia-bilingue.diptico.sec-formulario.resena'
                    )
                  "
                />
              </div>
              <div class="bottom">
                <div class="inside">
                  <div class="imagen-container">
                    <v-img
                      class="imagen"
                      src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto/v1639498861/terapiacl/servicios/terapia-bilingue/mensaje-2.png"
                    />
                  </div>
                  <form
                    action=""
                    @submit.prevent="validate()"
                  >
                    <validation-observer
                      ref="observer"
                      v-slot="{ invalid }"
                    >
                      <div class="form-main">
                        <validation-provider
                          v-slot="{ errors }"
                          rules="required|max:2"
                          name="Sucursal"
                        >
                          <v-select
                            v-model="sucursal"
                            :error-messages="errors"
                            :items="sucursales"
                            item-text="Nombre"
                            item-value="CodSucursal"
                            :label="
                              $t(
                                'servicios.terapia-bilingue.diptico.sec-formulario.etiqueta'
                              )
                            "
                            persistent-hint
                            filled
                            dense
                            hide-details
                            dark
                            :height="$vuetify.breakpoint.xs ? 41 : 54"
                          />
                        </validation-provider>
                        <v-btn
                          type="submit"
                          color="success"
                          :disabled="invalid"
                          :height="$vuetify.breakpoint.xs ? 41 : 54"
                        >
                          {{
                            $t(
                              'servicios.terapia-bilingue.diptico.sec-formulario.btn-texto'
                            )
                          }}
                        </v-btn>
                      </div>
                    </validation-observer>
                  </form>
                </div>
              </div>
            </div>
          </v-col>
          <v-col class="col-12 col-md-6 box-telefono">
            <div class="main">
              <div class="up">
                <h4
                  v-html="
                    this.$t(
                      'servicios.terapia-bilingue.diptico.sec-numeros.titulo'
                    )
                  "
                />
                <span
                  v-html="
                    this.$t(
                      'servicios.terapia-bilingue.diptico.sec-numeros.resena'
                    )
                  "
                />
              </div>
              <div class="bottom">
                <div class="inside">
                  <div class="imagen-container">
                    <v-img
                      class="imagen"
                      src="https://res.cloudinary.com/dkod5pdlc/image/upload/f_auto,q_auto/v1639498861/terapiacl/servicios/terapia-bilingue/telefono-2.png"
                    />
                  </div>
                  <div class="numbers-main">
                    <ul>
                      <li
                        v-for="(item, i) in sucursales"
                        :key="i"
                      >
                        <router-link :to="`/sucursales/${item.ToLink}`">
                          <b>{{ item.Nombre }}:</b>
                          {{ item.Telefono }}
                        </router-link>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </v-col>
        </v-row>
        <splitter-volver v-bind="{ text: $t('volver') }" />
      </div>
    </v-container>
  </div>
</template>
<script>
  import { mapState } from 'vuex'
  export default {
    name: 'TerapiaBilingue',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      Splitter: () => import('@/components/web/base/Splitter'),
      SplitterVolver: () => import('@/components/web/base/SplitterVolver'),
    },
    data () {
      return {
        sucursal: false,
        sucursalToLink: false,
        buttonsCharged: false,
        locale: 'en',
        especialidades: [],
        localeClass: {
          enSelected: true,
          esSelected: false,
        },
      }
    },
    computed: {
      ...mapState('web', ['sucursales']),
    },
    created () {
      this.$vuetify.rtl = false
      this.$i18n.locale = 'en'
    },
    methods: {
      changeLang (locale) {
        this.localeClass.enSelected = false
        this.localeClass.esSelected = false
        this.locale = locale
        switch (locale) {
          case 'en':
            this.localeClass.enSelected = true
            break
          case 'es':
            this.localeClass.esSelected = true
            break
        }
        this.$i18n.locale = `${locale}`
      },
      validate () {
        console.log(this.locale)
        this.$router.push({
          name: 'Formulario Terapia Bilingue',
          params: { sucursal: this.sucursal, locale: this.locale },
        })
      },
    },
    metaInfo: {
      title: 'Terapia Bilingue',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
<style lang="sass">
@import '@/sass/variables2'

@mixin langSpan($status)
  font-weight: bold
  margin-right: 5px
  margin-left: 10px
  @if($status == 'selected')
    color: $orange !important
  @else
    color: $lightblack !important

.langSpanNormal
  @include langSpan('normal')

.langSpanSelected
  @include langSpan('selected')
</style>
